.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.body{
  background-color: rgba(184, 166, 138, 0.39);
}
@media only screen and (min-width: 700px){
  #nav-brandd{
    width: 180px;
  }
}
@media only screen and (max-width: 650px){
  #nav-brandd{
    margin-right: 0px
  }
  #nav-signupp{
   margin-left: -5px; 
   margin-right: 0px;
   padding: 5px;
  }
  #nav-signinn{
    padding: 5px;
  }
}

